/* Reset and Base Styles */
/* You can add any global reset or base styles here */

/* Container Styles */
.container {
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 1rem;
    padding-right: 1rem;
  }
  
  /* Header Styles */
  header {
    background-color: #1a202c;
    color: #ffffff;
  }
  
  header a {
    color: #ffffff;
  }
  
  /* Navigation Menu Styles */
  nav {
    background-color: #2d3748;
    color: #ffffff;
  }
  
  nav a {
    padding: 0.75rem 1rem;
  }
  
  /* Footer Styles */
  footer {
    background-color: #1a202c;
    color: #ffffff;
    text-align: center;
    padding: 1rem 0;
  }
  
  /* Product Card Styles */
  .product-card {
    border: 1px solid #e2e8f0;
    border-radius: 0.5rem;
    overflow: hidden; /* Ensure images don't overflow */
  }
  
  .product-card img {
    height: auto; /* Make sure images are responsive */
    width: 100%;
    object-fit: cover;
  }
  
  .product-card h3 {
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 0.5rem;
  }
  
  .product-card p {
    color: #4a5568;
  }
  
  .product-card input[type="number"] {
    width: 4rem;
    padding: 0.5rem;
    border: 1px solid #e2e8f0;
    border-radius: 0.25rem;
    margin-right: 0.5rem;
  }
  
  .product-card button {
    background-color: #3182ce;
    color: #ffffff;
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 0.25rem;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
  }
  
  .product-card button:hover {
    background-color: #2c5282;
  }
  
  /* Cart Page Styles */
  .cart-page {
    margin-top: 2rem;
  }
  
  .cart-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 0;
    border-bottom: 1px solid #cbd5e0;
  }
  
  .cart-item:last-child {
    border-bottom: none;
  }
  
  .cart-item .item-details {
    flex: 1;
  }
  
  .cart-item h3 {
    font-size: 1.25rem;
    font-weight: 600;
    margin-bottom: 0.25rem;
  }
  
  .cart-item p {
    color: #4a5568;
  }
  
  .cart-item button {
    color: #e53e3e;
    cursor: pointer;
    transition: color 0.3s ease-in-out;
  }
  
  .cart-item button:hover {
    color: #c53030;
  }
  
  /* Thank You Page Styles */
  .thank-you-page {
    margin-top: 2rem;
    text-align: center;
  }
  
  .thank-you-page h2 {
    font-size: 2rem;
    font-weight: 600;
    margin-bottom: 1rem;
  }
  
  .thank-you-page p {
    font-size: 1.5rem;
    color: #4a5568;
  }
  
  /* Product Listing Page Styles */
  .product-listing-page {
    margin-top: 2rem;
  }
  
  .product-listing-page h2 {
    font-size: 2rem;
    font-weight: 600;
    margin-bottom: 1rem;
  }
  
  .product-listing-page .grid-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 1rem;
  }
  
  .product-listing-page .pagination {
    display: flex;
    justify-content: center;
    margin-top: 1rem;
  }
  
  .product-listing-page .pagination button {
    padding: 0.5rem 1rem;
    margin: 0 0.25rem;
    border-radius: 0.25rem;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
  }
  
  .product-listing-page .pagination button:hover {
    background-color: #3182ce;
    color: #ffffff;
  }
  
  .product-listing-page .pagination button.active {
    background-color: #3182ce;
    color: #ffffff;
  }
  /* Image Styles */
.img-container {
    position: relative;
    width: 100%;
    padding-top: 100%; /* Maintain aspect ratio (1:1) */
    overflow: hidden;
  }
  
  .img-container img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover; /* Ensure the image covers the entire container */
  }
  
  /* Responsive Utility Classes (optional) */
  @media (max-width: 768px) {
    .container {
      padding-left: 0.5rem;
      padding-right: 0.5rem;
    }
  
    .product-card img {
      height: 200px; /* Adjust image height for smaller screens */
    }
  
    .product-listing-page .grid-container {
      grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    }
  }
  